"use client";

import React, { useState } from "react";
import { useCategory } from "../../context/CategoryContext";
import styles from "./CategoryForm.module.css";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const CategoryForm = () => {
  const [name, setName] = useState("");
  const [budget, setBudget] = useState<number>(0);
  const [categoryType, setCategoryType] = useState<string>(""); // Başlangıçta "Gider türü seçiniz" olacak
  const [editIndex, setEditIndex] = useState<number | null>(null);
  const { categories, addCategory, updateCategory, removeCategory } = useCategory();

  const resetForm = () => {
    setName("");
    setBudget(0);
    setCategoryType(""); // Başlangıç değerini resetle
    setEditIndex(null);
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
  
    // Aynı isimde ancak farklı türde kategori eklenmesine izin veriyoruz
    const existingCategory = categories.find(
      (category) => category.name === name && category.type === categoryType
    );
  
    if (existingCategory && editIndex === null) {
      toast.error("Bu isim ve türde bir kategori zaten mevcut!");
      return;
    }
  
    if (editIndex !== null) {
      updateCategory(editIndex, { name, budget, type: categoryType });
      toast.success("Kategori başarıyla güncellendi!");
    } else if (name && (categoryType !== "" || budget > 0)) {
      addCategory({ name, budget, type: categoryType });
      toast.success("Kategori başarıyla eklendi!");
    }
  
    resetForm();
  };

  const handleDelete = (index: number) => {
    const isConfirmed = window.confirm("Bu işlemi silmek istediğinizden emin misiniz?");
    if (isConfirmed) {
      removeCategory(index);
      toast.error("Kategori başarıyla silindi!");
    }
  };

  const handleEdit = (index: number) => {
    setName(categories[index]?.name || "");
    setBudget(categories[index]?.budget || 0);
    setCategoryType(categories[index]?.type || ""); // Seçili türü ayarla
    setEditIndex(index);
  };

  return (
    <div className={styles.container}>
      <h2>Kategoriler</h2>
      <form onSubmit={handleSubmit} className={styles.form}>
        <input
          type="text"
          placeholder="Kategori Adı"
          value={name}
          onChange={(e) => setName(e.target.value)}
          className={styles.input}
        />
        
        {/* Kategori türü seçimi */}
        <select
          value={categoryType}
          onChange={(e) => setCategoryType(e.target.value)}
          className={styles.input}
        >
          <option value="">Kategori tipini seçiniz</option>
          <option value="Gelir">Gelir</option>
          <option value="Gider">Gider</option>
        </select>
 
        {categoryType === "Gider" && (
          <input
            type="number"
            placeholder="Bütçe"
            value={budget}
            onChange={(e) => setBudget(Number(e.target.value))}
            className={styles.input}
          />
        )}
        
        <button type="submit" className={styles.button}>
          {editIndex !== null ? "Güncelle" : "Ekle"}
        </button>
      </form>

      <table className={styles.table}>
        <thead>
          <tr>
            <th>Kategori Adı</th>
            <th>Türü</th>
            <th>Bütçe Limiti</th>
            <th>Düzenle</th>
            <th>Sil</th>
          </tr>
        </thead>
        <tbody>
          {Array.isArray(categories) && categories.length > 0 ? (
            categories.map((category, index) => (
              <tr key={index}>
                <td>{category?.name || "Bilinmiyor"}</td>
                <td>{category?.type || "Bilinmiyor"}</td>
                <td>
                  {category?.budget ? `${category.budget}₺` : "-"}
                </td>
                <td>
                  <button
                    className={styles.editButton}
                    onClick={() => handleEdit(index)}
                  >
                    Güncelle
                  </button>
                </td>
                <td>
                  <button
                    className={styles.deleteButton}
                    onClick={() => handleDelete(index)}
                  >
                    Sil
                  </button>
                </td>
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan={5} className={styles.noData}>
                Kategoriler bulunamadı
              </td>
            </tr>
          )}
        </tbody>
      </table>

      <ToastContainer position="top-right" autoClose={3000} hideProgressBar />
    </div>
  );
};

export default CategoryForm;
