 
import React from "react";
import { Bar } from "react-chartjs-2";
import { useBudget } from "../../context/BudgetContext";  
import { Chart as ChartJS, BarElement, CategoryScale, LinearScale, Tooltip, Legend } from "chart.js";
import styles from "./ChartPage.module.css"; // CSS modülü import

ChartJS.register(BarElement, CategoryScale, LinearScale, Tooltip, Legend);

export default function ChartPage() {
  const { transactions } = useBudget();

  // Gelir ve Giderleri Kategorilere Göre Gruplama
  const incomeData: { [key: string]: number } = {};
  const expenseData: { [key: string]: number } = {};

  transactions.forEach((transaction) => {
    if (transaction.type === "income") {
      incomeData[transaction.category] = (incomeData[transaction.category] || 0) + transaction.amount;
    } else if (transaction.type === "expense") {
      expenseData[transaction.category] = (expenseData[transaction.category] || 0) + transaction.amount;
    }
  });

  const categories = Array.from(
    new Set([...Object.keys(incomeData), ...Object.keys(expenseData)])
  );

  const data = {
    labels: categories,
    datasets: [
      {
        label: "Income",
        data: categories.map((category) => incomeData[category] || 0),
        backgroundColor: "rgba(75, 192, 192, 0.6)",
        borderColor: "rgba(75, 192, 192, 1)",
        borderWidth: 1,
      },
      {
        label: "Expense",
        data: categories.map((category) => expenseData[category] || 0),
        backgroundColor: "rgba(255, 99, 132, 0.6)",
        borderColor: "rgba(255, 99, 132, 1)",
        borderWidth: 1,
      },
    ],
  };

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top" as const,
      },
      tooltip: {
        callbacks: {
          label: (context: any) => {
            return `${context.dataset.label}: ${context.raw}`;
          },
        },
      },
    },
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  return (
    <div>
      <h1>Chart Page</h1>
      <div className={styles["chart-container"]}>
        <Bar data={data} options={options} />
      </div>
    </div>
  );
}
